@import url("https://fonts.googleapis.com/css2?family=Bangers&family=Monoton&family=Montserrat:wght@300;500&family=Poppins:wght@300&family=Roboto+Condensed&family=Roboto:wght@100;300&family=Shadows+Into+Light&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bangers&family=Herr+Von+Muellerhoff&family=Monoton&family=Montserrat:wght@300;500&family=Poppins:wght@300&family=Roboto+Condensed&family=Roboto:wght@100;300&family=Shadows+Into+Light&display=swap");
@import 'animate.css';
.logo h1 {
  font-family: "Herr Von Muellerhoff", cursive;
  font-weight: bold;
  font-size: 40px;
  letter-spacing: 3px;
  margin: 0;
  padding: 0;
  padding-left: 15px;
  color: #f6f7ff;
}

nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 8vh;
  margin-bottom: 100px;
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2c5364, #203a43, #0f2027); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2c5364, #203a43, #0f2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.navigation-list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 40%;
}
.navigation-list li {
  list-style: none;
}

.a-button {
  display: inline-block;
  position: relative;
}

.a-button::after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #fe8109;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.a-button:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.burger {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.burger div {
  width: 45px;
  height: 4px;
  background-color: #f6f7ff;
  margin: 3px;
}

.burger {
  display: none;
}

.hello {
  text-transform: uppercase;
  font-size: 150px;
  margin-left: 50px;
}

.orange {
  color: #fe8109;
}

.profile_text {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.code_link {
  margin-top: 20px;
  border: 2px solid #fe8109;
  padding: 5px 10px;
  width: 70px;
  border-radius: 5px;
  text-align: center;
  vertical-align: middle;
  transform: rotate(20deg);
}

.code_link:hover {
  background-color: #fe8109;
  transition: 0.33s;
  transform: rotate(-20deg);
}

.main_section {
  display: flex;
  justify-content: space-between;
}
.main_section .cv {
  margin-right: 150px;
}
.main_section .cv p {
  margin-bottom: 20px;
}

.download {
  transform: rotate(20deg);
}

.page-footer {
  clear: both;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  text-align: center;
  height: 250px;
  padding-top: 100px;
}

.social-media img {
  margin: 5px;
}

/* Footer icon animations */
.twitter-icon:hover,
.github-icon:hover,
.linkedin-icon:hover {
  transform: scale(1.3);
  -webkit-animation: 2s ease;
  animation: 2s ease;
}

.contact_card {
  width: 700px;
  height: 500px;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  box-shadow: 0.5px 0.5px 10px #14272f;
}

.first_card,
.second_card {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.first_card {
  position: relative;
  background-color: transparent;
}
.first_card h3 {
  position: absolute;
  top: 15px;
}

.contact_input {
  font: inherit;
  height: 35px;
  width: 80%;
  margin-top: 20px;
  font-weight: 500;
  font-size: 12px;
  opacity: 90%;
  background-color: transparent;
  transition: all 0.3s;
  padding: 7px;
  outline: 0;
  border: none;
  border-bottom: 2px solid #fe8109;
}

.submit_button {
  margin-top: 50px;
  font-size: 20px;
  font-weight: 500;
  border: none;
  padding: 0;
  background-color: #fe8109;
  color: #f6f7ff;
  cursor: pointer;
}
.submit_button:hover {
  opacity: 0.8;
  transform: scale(1.06);
  transition: ease-in-out 0.25s;
}

.text {
  height: 100px;
  resize: none;
}

.second_card {
  background: linear-gradient(to right, #ffc837, #ff8008);
  color: #f6f7ff;
  position: relative;
}
.second_card h3 {
  position: absolute;
  top: 15px;
}
.second_card ul {
  list-style: none;
}
.second_card li {
  padding: 10px;
}
.second_card span {
  padding-right: 10px;
  color: #f6f7ff;
}
.second_card a {
  color: #14272f;
}
.second_card a:hover {
  text-decoration: underline;
  color: #f6f7ff;
}

.profile {
  max-width: 700px;
  margin: 0 auto;
  height: 250px;
  text-align: center;
}

.profile-text {
  padding-top: 40px;
}

.profile_portrait {
  width: 250px;
  height: 250px;
  float: left;
  margin-right: 40px;
  border-radius: 50%;
  filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.19));
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.5) 0px 6px 6px;
}

.code-link {
  display: block;
  box-sizing: border-box;
}

/* Modals */
.modal_container_1,
.modal_container_2,
.modal_container_3,
.modal_container_4,
.modal_container_5,
.modal_container_6 {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  opacity: 0;
  transition: ease-in-out 0.33s;
}

.modal_active {
  opacity: 1;
  pointer-events: auto;
}

.modal_1,
.modal_2,
.modal_3,
.modal_4,
.modal_5,
.modal_6 {
  background-color: #f6f7ff;
  max-width: 100%;
  width: 60%;
  height: 90%;
  border-radius: 5px;
  overflow: scroll;
  position: relative;
}

.modal_heading,
.modal_list,
.p_modal {
  text-align: center;
  color: #14272f;
}

.modal_section {
  display: flex;
  padding: 15px;
  opacity: 0.7;
}

.modal_text,
.modal_list {
  width: 50%;
}

.modal_list li {
  list-style: none;
}

.modal_buttons {
  display: flex;
  justify-content: space-between;
  padding-bottom: 15px;
  padding-left: 15px;
}

.content_buttons {
  margin-top: 10px;
}

.content_buttons a {
  margin-left: 15px;
}

.modal_close_button {
  margin-right: 15px;
}

.close {
  padding: 0px;
}

.a-button-modal {
  color: #14272f;
}

.a-button-modal:hover {
  color: #0575e6;
  text-decoration: underline;
  transition: 0.3s ease;
}

.x_modal {
  position: absolute;
  top: 10px;
  right: 20px;
  font-weight: bold;
  color: #f6f7ff;
  cursor: pointer;
}

.x_modal4,
.x_modal5,
.x_modal6 {
  color: #14272f;
}

/* MODAL PICS*/
.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

/* Hide the images by default */
.mySlides {
  display: none;
}

/* Next & previous buttons */
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: #0575e6;
  font-weight: bold;
  font-size: 18px;
  transition: 0.3s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

.prev:hover,
.next:hover {
  transform: scale(1.3);
}

.modal_text p {
  color: #14272f;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* Number text (1/3 etc) */
.numbertext {
  color: #0575e6;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
body {
  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2c5364, #203a43, #0f2027); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #538194, #203a43, #0f2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.grid {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 15px 1fr 15px 1fr 15px 1fr;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-gap: 15px;
  width: 92.5%;
  margin: 0 auto;
}
.grid .grid_item {
  background: whitesmoke;
  color: #14272f;
  height: 200px;
  padding: 10px;
  margin: 0;
  text-align: center;
  border-radius: 0.35rem;
}
.grid .grid_item .button_div {
  padding-top: 35px;
}

.grid_item:hover {
  transform: scale(1.05);
  transition: 0.5s ease;
  background: opacity 0.5;
  box-shadow: 0.5px 0.5px 10px #fe8109;
}

.invisible {
  opacity: 0;
}

.visible {
  opacity: 1;
}

.grid_links {
  display: inline-block;
}

.work {
  text-align: center;
  margin-bottom: 50px;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  background-color: #f6f7ff;
  color: #f6f7ff;
}
body h1 {
  font-size: 25px;
  font-weight: 700;
}

a {
  color: #f6f7ff;
  text-decoration: none;
  font-weight: bold;
}
a:hover {
  cursor: pointer;
}

button {
  border: none;
  font-family: inherit;
  display: inline-block;
  background-color: #14272f;
  color: #f6f7ff;
  text-decoration: none;
  text-align: center;
  align-items: center;
  padding: 0px;
  border-radius: 3px;
  height: 35px;
  width: 60px;
  -webkit-transition: 0.5s ease;
  -o-transition: 0.5s ease;
  transition: 0.5s ease;
}
button:hover {
  opacity: 0.77;
}

/* Media queries */
@media screen and (max-width: 1064px) {
  .navigation-list {
    width: 60%;
  }
}
@media screen and (max-width: 710px) {
  body {
    overflow-x: hidden;
  }
  .navigation-list {
    position: absolute;
    right: 0px;
    height: 92vh;
    top: 8vh;
    background-color: #2c5364;
    display: flex;
    flex-direction: column;
    width: 50%;
    align-items: center;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
  }
  .burger {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 15px;
  }
  .burger div {
    width: 35px;
    height: 4px;
    background-color: #f6f7ff;
    margin: 3px;
  }
  .nav-active {
    transform: translateX(0%);
  }
}
.cursor {
  z-index: 999;
  position: fixed;
  background-color: #ffc837;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  pointer-events: none;
  box-shadow: 0 0 10px #fe8109, 0 0 60px #fe8109, 0 0 100px #ffc837;
  animation: colors 4s infinite;
  transform: translate(-50%, -50%);
  display: none;
}

.cursor:before {
  content: "";
  position: absolute;
  background-color: #ffc837;
  width: 40px;
  height: 40px;
  opacity: 0.3;
  transform: translate(-30%, -30%);
  border-radius: 50%;
}

@keyframes colors {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}